@import url('https://fonts.googleapis.com/css2?family=Oxygen&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Oxygen', sans-serif !important;
}

html {
    font-size: 62.5%;
}

.App {
    position: relative;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.App::-webkit-scrollbar {
    display: none;
  }